import user from '@/store/modules/user.js'


import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
	state: {
		orderType: 'takein',
		address: {},
	},
	mutations: {
		SET_ORDER_TYPE(state, type) {
			state.orderType = type
		},
		SET_ADDRESS(state, address) {
			state.address = address
		},
	},
	modules: {
		user
	},
	strict: true
})













export default store
